import s from './RegisterForm.module.css';
import { useForm } from 'react-hook-form';
import { ToastContainer, toast } from 'react-toastify';

// import { useCreateUserMutation } from 'redux/contactsApi/contactsApi';

import 'react-toastify/dist/ReactToastify.css';

export default function RegisterForm() {
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm();

  // const [fetchUser, data] = useCreateUserMutation();
  // console.log(data);

  const handleClick = errors => {
    if (errors.name)
      toast.error(errors.name.message, {
        position: toast.POSITION.TOP_RIGHT,
      });

    if (errors.email)
      toast.error(errors.email.message, {
        position: toast.POSITION.TOP_RIGHT,
      });

    if (errors.password)
      toast.error(errors.password.message, {
        position: toast.POSITION.TOP_RIGHT,
      });
  };
  const onSubmit = data => {
    console.log(data);
    // console.log(fetchUser(JSON.stringify(data)));
    console.log(data);
    reset();
  };

  return (
    <>
      <ToastContainer />
      <form onSubmit={handleSubmit(onSubmit)} className={s.form}>
        <input
          {...register('phone', {
            required: '    ',
            pattern: {
              value:
                /^[a-zA-Z--]+(([' -][a-zA-Z-- ])?[a-zA-Z--]*)*$/,
              message: '     ',
            },
          })}
          type="text"
          name="phone"
          placeholder="Phone"
          autoComplete="phone"
          className={`${s.input} ${errors.name ? s.invalid : s.valid}`}
        />
        <p>{errors.name?.message}</p>

        <input
          {...register('email', {
            required: '    ',
            pattern: {
              value: /^[\w.-]+@[a-zA-Z_-]+?(?:\.[a-zA-Z]{2,6})+$/,
              message: '       @',
            },
          })}
          type="text"
          name="email"
          placeholder="Email"
          autoComplete="email"
          className={`${s.input} ${errors.email ? s.invalid : s.valid}`}
        />
        <p>{errors.email?.message}</p>

        <input
          {...register('password', {
            required: '    ',
            pattern: {
              value: /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{4,16}$/,
              message:
                '    :   4  16 ,     ,        ',
            },
          })}
          type="password"
          name="password"
          placeholder="Password"
          autoComplete="password"
          className={`${s.input} ${errors.password ? s.invalid : s.valid}`}
        />
        <p>{errors.password?.message}</p>

        <input
          type="submit"
          value="Register"
          onClick={() => handleClick(errors)}
        />
      </form>
    </>
  );
}
